@import "vars";
@import "libs";
@import "fonts";

::placeholder {
	color: #9e9e9e; }
html, body {
	height: 100%; }
.main {
	flex: 1 0 auto; }
html {
	font-size: 2.66666vw; }
body {
	font-size: 1.8rem;
	min-width: 320px;
	position: relative;
	line-height: 1.33;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	display: flex;
	flex-direction: column;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; } }
b, strong {
	font-weight: 700; }
p {
	margin-bottom: 0; }
a {
	color: $text;
	text-decoration: none;
	transition: 0.25s;
	&:hover {
		color: inherit;
		text-decoration: underline; }
	&[href^="#"], &[href^="tel"], &[href^="mail"] {
		&:hover {
			text-decoration: none; } } }
.w-100 {
	width: 100%; }
.h-100 {
	height: 100%; }
ol, ul {
	@include list-unstyled; }
.icon {
	display: inline-block;
	vertical-align: middle;
	bottom: 0.2rem;
	position: relative;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-left {
		margin-right: 1rem; }
	&-right {
		margin-left: .8rem; } }
.img {
	display: block;
	position: absolute;
	@include size(100%);
	&-con {
		object-fit: contain; }
	&-cov {
		object-fit: cover; }
	&-wrapper {
		position: relative; } }
img, picture, figure {
	@include img-fluid; }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: .75em;
	line-height: 1.2;
	font-weight: 400;
	font-family: $title-font;
	color: $accent; }
h1, .h1 {
	font-size: 3.4rem; }
h2, .h2 {
	font-size: 3rem; }
h3, .h3 {
	font-size: 2.6rem; }
h4, .h4 {
	font-size: 2.2rem; }
h5, .h5 {
	font-size: 1.8rem; }
h6, .h6 {
	font-size: 1.6rem; }
.text {
	&-xs {}
	&-s {}
	&-m {}
	&-l {}
	&-xl {} }
.btn {
	display: inline-flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	&:hover {
		text-decoration: none; }
	&-m {
		height: 5rem;
		border-radius: 2.5rem;
		padding-left: 4rem;
		padding-right: 4rem;
		min-width: 20rem;
		font-weight: 700;
		font-size: 1.6rem; }
	&-trans {
		border: .2rem solid $accent;
		color: $accent;
		&:hover {
			background-color: $accent;
			color: #ffffff; } } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.container {
	max-width: 145rem; }
.section {
	&__title {
		font-size: 7.2rem;
		margin-bottom: 3.5rem;
		text-align: center; } }
.form {
	display: block;
	&-control {
		background: #fff;
		border: .1rem solid $accent;
		height: 6.5rem;
		line-height: 5.8rem;
		padding: 0 2.4rem;
		color: #000000;
		box-shadow: inset 0px 4px 26px rgba(0, 0, 0, 0.08); }
	textarea.form-control {
		height: 53.3rem;
		line-height: 1.5;
		padding-top: 1rem;
		padding-bottom: 1rem; }
	&-label {
		display: block;
		font-family: $title-font;
		font-size: 3.2rem;
		color: $accent;
		padding-left: 2rem;
		padding-right: 2rem;
		margin-bottom: 1.3rem; }
	&-group {
		margin-bottom: 6rem;
		.form-label {
			text-align: center;
			margin-bottom: 2.4rem; } }
	.row {
		@include make-row(7rem);
		> * {
			padding-left: 3.5rem;
			padding-right: 3.5rem; } }
	&-submit {
		text-align: center;
		margin-top: 7.4rem; } }
// header
.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: #ffffff;
	z-index: 10;
	padding: {
		top: 1rem;
		bottom: 1rem; }
	border-bottom: .1rem solid $accent; }
.logo {
	display: flex;
	align-items: center;
	a {
		display: block;
		margin-right: 1.3rem; }
	img {
		width: 15.1rem;
		height: auto; } }
.slogan {
	display: block;
	font-family: $title-font;
	font-size: 3.2rem;
	color: $accent; }
.menu {
	&-list {
		@include make-row(3.4rem);
		&__item {
			padding-left: 1.7rem;
			padding-right: 1.7rem; }
		&__link {
			font-size: 1.6rem;
			&:hover {
				color: $accent;
				text-decoration: none; } } } }
// header

// promo
.promo {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-color: #000000;
	color: #ffffff;
	margin-top: 8rem;
	margin-bottom: 5.3rem;
	.container {
		height: 76rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-top: 4.3rem; }
	.btn {
		color: #ffffff;
		align-self: self-start; }
	&__title {
		font-size: 9.6rem;
		margin-bottom: 1.5rem; }
	p {
		font-size: 2.8rem;
		font-weight: 700;
		margin-bottom: 7rem;
		em {
			display: block;
			font-size: 3.9rem; }
		b {
			color: $accent;
			font-size: 3.9rem; } } }
// promo

// mission
.section--mission {
	padding-bottom: 8.8rem;
	position: relative;
	max-width: 1920px;
	margin-left: auto;
	margin-right: auto;
	.row {
		&:nth-child(odd) {
			flex-direction: row-reverse;
			.mission-img {
				left: 10rem; } }
		&:not(:last-child) {
			margin-bottom: 10rem; } } }
.mission {
	&-img {
		position: absolute;
		height: 100%;
		width: calc(100vw - 10rem);
		max-width: 96rem;
		object-fit: cover;
		right: 10rem;
		&-wrapper {
			position: relative; } }
	&-content {
		padding-top: 4.2rem;
		padding-bottom: 7.5rem;
		position: relative;
		z-index: 1;
		.section__title {
			text-align: left;
			margin-bottom: 2.8rem; }
		p {
			color: $blue;
			font-weight: 700;
			&:not(:last-child) {
				margin-bottom: 3.1rem; } } }
	&-back {
		&-1, &-2 {
			position: absolute;
			pointer-events: none;
			display: block;
			z-index: -1; }
		&-1 {
			top: 57.5rem;
			left: 9.4rem;
			@include size(12.5rem,11.2rem); }
		&-2 {
			right: 0;
			bottom: 0;
			@include size(36.2rem,23.7rem); } } }
// mission

// blog
.section--blog {
	position: relative;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	.container {
		padding-top: 9rem;
		padding-bottom: 11rem;
		position: relative;
		.blog-back {
			position: absolute;
			pointer-events: none;
			display: block;
			top: 17.1rem;
			left: 24.7rem;
			@include size(12.5rem,11.2rem); } }
	.section__title {
		margin-bottom: 5.4rem; } }
.article {
	&-item {
		&__img {
			padding-bottom: 63%;
			margin-bottom: 1.4rem; }
		&__date {
			display: block;
			margin-bottom: .7rem;
			color: #183151;
			font-size: 1.4rem; }
		&__title {
			margin-bottom: .8rem;
			font-size: 3.6rem; }
		p {
			color: $blue;
			margin-bottom: 1.6rem;
			line-height: 1.28;
			font-size: 1.4rem;
			font-weight: 700;
			max-width: 29.6rem; } }
	&-carousel {
		position: relative;
		z-index: 2;
		.owl {
			&-nav {
				position: absolute;
				top: 50%;
				left: 50%;
				width: calc(100% + 6.2rem);
				transform: translate(-50%, -50%);
				display: flex;
				justify-content: space-between;
				pointer-events: none; }
			&-prev, &-next {
				display: block;
				pointer-events: auto;
				@include size(1.1rem,3.4rem);
				background: url(../img/data/arrow.svg) center no-repeat!important;
				transition: .25s;
				&.disabled {
					opacity: 0; }
				span {
					display: none; } }
			&-next {
				transform: rotate(-180deg); } } } }
// blog

// video
.section--video {
	padding-top: 8.7rem;
	padding-bottom: 6rem;
	max-width: 1920px;
	.container {
		max-width: none;
		padding-left: 9.4rem;
		padding-right: 9.4rem; }
	.section__title {
		margin-bottom: 4.6rem; } }
.video {
	&-carousel {
		.owl-stage-outer {
			padding-top: 4rem;
			padding-bottom: 4rem; }
		.center {
			z-index: 2;
			position: relative;
			.video-block {
				transform: scale(1.15,1.15);
				box-shadow: 0 .4rem .4rem rgba(0, 0, 0, 0.25);
				&__play {
					opacity: 1; } } } }
	&-block {
		padding-bottom: 68%;
		transition: 0.25s;
		&__play {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 2;
			transform: translate(-50%, -50%);
			background-position: center;
			background-repeat: no-repeat;
			background-color: $accent;
			@include size(7.3rem);
			border-radius: 100%;
			box-shadow: 0 .4rem .4rem rgba(0, 0, 0, 0.25);
			opacity: 0;
			transition: 0.25s; } } }

// video

// faq
.section--faq {
	padding-top: 3rem;
	padding-bottom: 6rem;
	.section__title {
		margin-bottom: 7.2rem; } }
.faq {
	&-item {
		background-color: #fff;
		box-shadow: 0 0 3.2rem rgba(0, 0, 0, 0.08);
		text-align: center;
		cursor: pointer;
		padding: 2rem 5.5rem 3.2rem;
		position: relative;
		&.is-open {
			&:after {
				transform: translateX(-50%) rotate(-180deg);
				bottom: 3.5rem; } }
		&:after {
			content: '';
			@include size(.9rem);
			display: block;
			position: absolute;
			left: 50%;
			transform: translateX(-50%) rotate(0deg);
			transition: 0.25s;
			background: url(../img/data/faq-toggle.svg) center no-repeat;
			bottom: 1rem; }
		&:not(:last-child) {
			margin-bottom: 2rem; }
		&__title {
			display: block;
			text-align: center;
			font-size: 3.6rem;
			color: $accent;
			font-family: $title-font; }
		&__toggle {
			display: none; }
		&__content {
			padding-top: 4rem;
			padding-bottom: 6rem;
			.faq-item__title {
				margin-bottom: 1.9rem;
				&:before {
					content: '';
					@include size(8.2rem,.79rem);
					background: url(../img/data/faq-line.svg) center no-repeat;
					display: block;
					margin: 0 auto 2.2rem; } }
			p {
				color: $blue;
				line-height: 1.5;
				&:not(:last-child) {
					margin-bottom: 2rem; } } } }
	&-back {
		&-1, &-2 {
			position: absolute;
			pointer-events: none;
			display: block;
			z-index: -1; }
		&-1 {
			top: 4rem;
			right: -20.9rem;
			@include size(29.3rem,36.1rem); }
		&-2 {
			top: 0;
			left: -15rem;
			@include size(18.8rem,24.6rem); } } }
// faq

// question
.section--question {
	padding-top: 2rem;
	padding-bottom: 12rem;
	.section__title {
		margin-bottom: 5.5rem; } }
.question {
	box-shadow: 0 0 3.2rem rgba(0, 0, 0, 0.08);
	padding: 5.5rem 5rem 10rem; }
// question

// order
.section--order {
	background-color: #fdfaf5;
	position: relative;
	.row {
		@include make-row(4.8rem);
		> * {
			padding-left: 2.4rem;
			padding-right: 2.4rem; } }
	.form {
		margin-top: 2.8rem;
		max-width: 77.2rem;
		&-label {
			margin-bottom: 2.3rem; }
		&-submit {
			text-align: left;
			margin-top: 4.7rem; } }
	.section__title {
		margin-bottom: 4rem;
		text-align: left;
		font-size: 6.8rem; } }
.order {
	&-img {
		@include size(48rem,71.3rem);
		margin-bottom: -11.1rem; }
	&-content {
		padding-top: 5.9rem;
		padding-bottom: 6.3rem;
		p {
			font-size: 1.7rem;
			color: $blue;
			line-height: 1.53;
			font-weight: 700;
			max-width: 77.2rem; } }
	&-back {
		position: absolute;
		pointer-events: none;
		display: block;
		top: 11.1rem;
		right: 0;
		@include size(32.4rem,48.1rem); } }
// order
@import "media";
