.section--blog .container { padding-top: 9rem; padding-bottom: 11rem; position: relative; }

.section--blog .container .blog-back { position: absolute; pointer-events: none; display: block; top: 17.1rem; left: 24.7rem; width: 12.5rem; height: 11.2rem; }

.section--blog .section__title { margin-bottom: 5.4rem; }

.article-item__img { padding-bottom: 63%; margin-bottom: 1.4rem; }

.article-item__date { display: block; margin-bottom: .7rem; color: #183151; font-size: 1.4rem; }

.article-item__title { margin-bottom: .8rem; font-size: 3.6rem; }

.article-item p { color: #617793; margin-bottom: 1.6rem; line-height: 1.28; font-size: 1.4rem; font-weight: 700; max-width: 29.6rem; }

.article-carousel { position: relative; z-index: 2; }

.article-carousel .owl-nav { position: absolute; top: 50%; left: 50%; width: calc(100% + 6.2rem); transform: translate(-50%, -50%); display: flex; justify-content: space-between; pointer-events: none; }

.article-carousel .owl-prev, .article-carousel .owl-next { display: block; pointer-events: auto; width: 1.1rem; height: 3.4rem; background: url(../img/data/arrow.svg) center no-repeat !important; transition: .25s; }

.article-carousel .owl-prev.disabled, .article-carousel .owl-next.disabled { opacity: 0; }

.article-carousel .owl-prev span, .article-carousel .owl-next span { display: none; }

.article-carousel .owl-next { transform: rotate(-180deg); }

.section--video { padding-top: 8.7rem; padding-bottom: 6rem; max-width: 1920px; }

.section--video .container { max-width: none; padding-left: 9.4rem; padding-right: 9.4rem; }

.section--video .section__title { margin-bottom: 4.6rem; }

.video-carousel .owl-stage-outer { padding-top: 4rem; padding-bottom: 4rem; }

.video-carousel .center { z-index: 2; position: relative; }

.video-carousel .center .video-block { transform: scale(1.15, 1.15); box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25); }

.video-carousel .center .video-block__play { opacity: 1; }

.video-block { padding-bottom: 68%; transition: 0.25s; }

.video-block__play { position: absolute; top: 50%; left: 50%; z-index: 2; transform: translate(-50%, -50%); background-position: center; background-repeat: no-repeat; background-color: #BA9151; width: 7.3rem; height: 7.3rem; border-radius: 100%; box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25); opacity: 0; transition: 0.25s; }

.section--faq { padding-top: 3rem; padding-bottom: 6rem; }

.section--faq .section__title { margin-bottom: 7.2rem; }

.faq-item { background-color: #fff; box-shadow: 0 0 3.2rem rgba(0, 0, 0, 0.08); text-align: center; cursor: pointer; padding: 2rem 5.5rem 3.2rem; position: relative; }

.faq-item.is-open:after { transform: translateX(-50%) rotate(-180deg); bottom: 3.5rem; }

.faq-item:after { content: ''; width: 0.9rem; height: 0.9rem; display: block; position: absolute; left: 50%; transform: translateX(-50%) rotate(0deg); transition: 0.25s; background: url(../img/data/faq-toggle.svg) center no-repeat; bottom: 1rem; }

.faq-item:not(:last-child) { margin-bottom: 2rem; }

.faq-item__title { display: block; text-align: center; font-size: 3.6rem; color: #BA9151; font-family: "AmadeusAP", sans-serif; }

.faq-item__toggle { display: none; }

.faq-item__content { padding-top: 4rem; padding-bottom: 6rem; }

.faq-item__content .faq-item__title { margin-bottom: 1.9rem; }

.faq-item__content .faq-item__title:before { content: ''; width: 8.2rem; height: 0.79rem; background: url(../img/data/faq-line.svg) center no-repeat; display: block; margin: 0 auto 2.2rem; }

.faq-item__content p { color: #617793; line-height: 1.5; }

.faq-item__content p:not(:last-child) { margin-bottom: 2rem; }

.faq-back-1, .faq-back-2 { position: absolute; pointer-events: none; display: block; z-index: -1; }

.faq-back-1 { top: 4rem; right: -20.9rem; width: 29.3rem; height: 36.1rem; }

.faq-back-2 { top: 0; left: -15rem; width: 18.8rem; height: 24.6rem; }

.section--question { padding-top: 2rem; padding-bottom: 12rem; }

.section--question .section__title { margin-bottom: 5.5rem; }

.question { box-shadow: 0 0 3.2rem rgba(0, 0, 0, 0.08); padding: 5.5rem 5rem 10rem; }

.section--order { background-color: #fdfaf5; position: relative; }

.section--order .row { display: flex; flex-wrap: wrap; margin-right: -2.4rem; margin-left: -2.4rem; }

.section--order .row > * { padding-left: 2.4rem; padding-right: 2.4rem; }

.section--order .form { margin-top: 2.8rem; max-width: 77.2rem; }

.section--order .form-label { margin-bottom: 2.3rem; }

.section--order .form-submit { text-align: left; margin-top: 4.7rem; }

.section--order .section__title { margin-bottom: 4rem; text-align: left; font-size: 6.8rem; }

.order-img { width: 48rem; height: 71.3rem; margin-bottom: -11.1rem; }

.order-content { padding-top: 5.9rem; padding-bottom: 6.3rem; }

.order-content p { font-size: 1.7rem; color: #617793; line-height: 1.53; font-weight: 700; max-width: 77.2rem; }

.order-back { position: absolute; pointer-events: none; display: block; top: 11.1rem; right: 0; width: 32.4rem; height: 48.1rem; }

/*==========  Desktop First  ==========*/
/*==========  Mobile First  ==========*/
@media (min-width: 992px) { html { font-size: 0.71428vw; } }

@media (min-width: 1500px) { html { font-size: 10px; } }
